import axios, { AxiosRequestConfig } from 'axios'
import env from 'UTILS/env'
import { SwConfigOptions } from 'TYPES/Widget'
import { getBrowserLanguage } from 'UTILS/dom'

const { apiHost } = env

const get = async (url: string, config?: AxiosRequestConfig) => {
  const res = await axios.get(url, config)
  if (res && res.data) {
    return res.data
  }
  return null
}

const checkToken = (token: string) => {
  if (token === '') {
    const error = ' Token'
    alert(`${error}`)
    console.error(`${error}`)
    console.error(
      ' https://www.seniverse.com/widgetv3  token window.SeniverseWeatherWidget '
    )
    console.error(
      ' token  App '
    )
  }
}

const getConfig = (option: SwConfigOptions) => {
  const { token, unit, language, location, geolocation } = option
  checkToken(token)

  return get(`${apiHost}/api/weather/${token}`, {
    params: {
      unit,
      language,
      location,
      geolocation,
      detected: getBrowserLanguage()
    }
  })
}

export default {
  getConfig
}
